<template>
  <div v-show="show" class="text-xs-left gridlast">
    <v-progress-circular 
      @click="resetCountDown()"
      v-ripple
      :rotate="360"
      :size="size"
      :width="width"
      :value="value"
      :color="color"                   
    >
      {{ progressDisplay }}{{progressUnit}}
    </v-progress-circular>
  </div>
</template>

<script>

export default {
  name: 'CobRefresh',
  props: {
    time: {   
      type: Number,
      default: 60 //valor em segundos
    },
    step: {
      type: Number,
      default: 1  //valor em segundos
    },
    show: {
      type: Boolean,
      default:true
    },
    size: {
      type: Number,
      default:23
    },
    width: {
      type: Number,
      default: 3
    },
    color: {
      type: String,
      default: "#999"
    }
  },
  data () {
    return {
      timer: {},
      value:0,
      running: true
    }
  },
  mounted () {
    this.initCountDown();
  },
  computed: {
    remainingTime () {
      return this.time * this.value / 100;
    },
    scale () {
        return this.remainingTime >= 3600 ? 3600 : this.remainingTime >= 60 ? 60 : 1;
    },
    progressDisplay () {
        return Math.floor(this.remainingTime/this.scale);
    },
    progressUnit () {
        return this.scale === 3600 ? 'h' : this.scale === 60 ? 'm' : '' //segundos não vale a pena mostrar;
    }
  },
  methods: {
    initCountDown (){
      this.value = 0;
      this.timer = setInterval(this.countDown, this.step * 1000)
    },
    recursiveForceRefresh(me) {
      if (me.forceRefresh !== undefined) me.forceRefresh();
      me.$children.forEach(c => this.recursiveForceRefresh(c))
    },
    recursiveUpdateValue(me) {
      if (me.updateValue !== undefined) me.updateValue();
      me.$children.forEach(c => this.recursiveUpdateValue(c));
    },
    resetCountDown() {
      clearInterval(this.timer);
      this.initCountDown();
      this.recursiveForceRefresh(this.$parent);
    },
    countDown() {
      //Só corre se estiver visível
      if(this.running && this.$el && this.$el.isConnected) {
        var iteration = this.step * 100/this.time
        if (this.value > 0) {
          this.value -= iteration ;
        } else {
          this.value = 100;
          this.recursiveUpdateValue(this.$parent);
        }
      }
    }
  }
}
</script>

<style scoped>
  .v-progress-circular {
    font-size:8px;
    border-radius: 9px;
    cursor: pointer;
  }
  .gridlast {
    grid-row-end:-1/1;
  }
</style>