<template>
    <v-card outlined class="fill-height" >
        <v-card-title class="blue--text">
            {{finalTitle}}
        </v-card-title>

        <v-card-text>
            <div v-for="n in finalLines" :key="n">
                {{makeText(Math.random()*maxLength)}}
            </div>
        </v-card-text>

    </v-card>
</template>

<script>
    export default {
        name: "BoardPlaceholder",
        props: {
            title: {
                type: String,
                default: ""
            },
            lines: {
                type: Number,
                default: 0
            },
            maxLength: {
                type: Number,
                default: 50
            }
        },
        data: () => ({
            finalTitle: String,
            finalLines: Number
        }),
        created() {
            this.updateValue()
        },
        methods: {
            makeText(length) {
                var result           = '';
                var characters       = ' ABCDEFGHIJKLMNOPQRSTUVWXYZ abcdefghijklmnopqrstuvwxyz 0123456789 ';
                var charactersLength = characters.length;
                for ( var i = 0; i < length; i++ ) {
                    result += characters.charAt(Math.floor(Math.random() * charactersLength));
                }
                return result;
            },
            updateValue() {
                this.finalTitle = this.title ? this.title : this.makeText(Math.random()*20)
                this.finalLines = this.lines && this.lines != 0 ? this.lines : Math.round(1+Math.random()*10)
            },
        }
    }
</script>

<style>
  .v-application--wrap {
    min-height: unset !important;
  }
</style>